"use client";

import React, { useState } from "react";
import Link from "next/link";

export default function HomePage() {
  const [selectedElo, setSelectedElo] = useState(500);

  const bots = [
    { name: "Bot Novato", elo: 500 },
    { name: "Bot Intermedio", elo: 1000 },
    { name: "Bot Avanzado", elo: 1500 },
    { name: "Bot Experto", elo: 2000 },
    { name: "Bot Maestro", elo: 2500 },
    { name: "Bot Gran Maestro", elo: 2900 },
  ];

  return (
    <div style={{ padding: "2rem", maxWidth: "900px", margin: "0 auto" }}>
      <header style={{ textAlign: "center", marginBottom: "2rem" }}>
        <h1 style={{ fontSize: "2rem", fontWeight: "bold" }}>Bienvenido al Chess AI</h1>
        <p style={{ fontSize: "1rem", color: "#555" }}>
          Selecciona un bot según su nivel de ELO para comenzar a jugar
        </p>
      </header>

      <div style={{ display: "flex", flexDirection: "column", gap: "1rem" }}>
        {bots.map((bot) => (
          <button
            key={bot.elo}
            onClick={() => setSelectedElo(bot.elo)}
            style={{
              padding: "1rem",
              fontSize: "1rem",
              fontWeight: selectedElo === bot.elo ? "bold" : "normal",
              backgroundColor: selectedElo === bot.elo ? "#4CAF50" : "#eee",
              color: selectedElo === bot.elo ? "#fff" : "#000",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              transition: "all 0.2s",
            }}
            onMouseOver={(e) => (e.currentTarget.style.backgroundColor = selectedElo === bot.elo ? "#45a049" : "#ddd")}
            onMouseOut={(e) => (e.currentTarget.style.backgroundColor = selectedElo === bot.elo ? "#4CAF50" : "#eee")}
          >
            {bot.name} — ELO: {bot.elo}
          </button>
        ))}
      </div>

      <div style={{ textAlign: "center", marginTop: "2rem" }}>
        <Link href={`/play?botElo=${selectedElo}`}>
          <button
            style={{
              padding: "1rem 2rem",
              fontSize: "1rem",
              fontWeight: "bold",
              backgroundColor: "#2196F3",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              transition: "all 0.2s",
            }}
            onMouseOver={(e) => (e.currentTarget.style.backgroundColor = "#0b7dda")}
            onMouseOut={(e) => (e.currentTarget.style.backgroundColor = "#2196F3")}
          >
            Jugar con Bot ELO {selectedElo}
          </button>
        </Link>
      </div>
    </div>
  );
}
